body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
}

.header-container{
  padding: 20px;
  background-color: black;
  color: white;
  text-align: center;
}

.header-text{
  font-weight: bold;
  font-size: 25px;
  letter-spacing: 4px;
}
.score-box{
  text-align: right;
  padding: 10px 8%;
}
.score-board{
  display: inline-block;
  padding: 25px;
  background-color: #f9f93b;
  text-align: center;
  font-weight: bold;
}
.main-container{
  min-height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 40px;
}
.revealer{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  background-color: rgb(132, 255, 132);
  font-weight: bold;
  font-size: 20px;
}
.input-box{
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.input-box input{
  padding: 8px;
  border: black solid 1px;
  width: 70px;
  background-color: rgb(226, 226, 226);
  font-weight: bold;
  font-size: 20px;
}
.input-box button{
  align-self: center;
  padding: 5px 10px;
  background-color: black;
  color: white;
}
.suggestion-box{
  padding: 10px 20px;
  background-color: rgb(0, 0, 71);
  color: white;
  border-radius: 6px;
}